// react
import React from "react";
// import PropTypes from "prop-types";
// zerod
import { Zlayout, ZmainHOC } from "zerod";
import { BrowserRouter ,HashRouter,  Route } from "react-router-dom";
import { Menu, Dropdown, Icon } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import Main from "@/views/Main";
import cssClass from "@/views/Main/style.scss";
import doc from "zerod/components/ZmainHOC/doc.md";
import publiced  from '../../../../../config/pablic';

const Router =publiced.path=="/"?BrowserRouter:HashRouter;

const showMain = function(Component) {
	return (
		<div
			style={{ height: "100%" }}
		>
			<Router>
				<Route path={`/main/HOC-doc/ZmainHOC-doc`} component={Component} />
			</Router>
		</div>
	);
};
export default AmdDocHOC(doc, {
	demo1: function() {
		const MyScript = AshowDemoHOC(showMain(Main), false);
		return <MyScript />;
	},
	demo2: function() {
		const pageConfig = {
			// 左侧边展开时的宽度
			leftExpandWidth: 280,
			// 主页的主题 light | dark
			theme: "dark",
			// 顶部栏左边内容的渲染钩子
			headerLeftRender: (main) => {
				return <Zlayout.ZheaderBtn className="z-margin-right-15">左边</Zlayout.ZheaderBtn>;
			},
			// 顶部栏右边内容的渲染钩子
			headerRightRender: (main) => {
				return (
					<Zlayout.Template>
						<Zlayout.ZheaderBtn className="z-margin-right-15">按钮1</Zlayout.ZheaderBtn>
						<Zlayout.ZheaderBtn className="z-margin-right-15">按钮2</Zlayout.ZheaderBtn>
						<UserDropdown />
					</Zlayout.Template>
				);
			},
			// 组件加载完成的钩子
			componentDidMount: (callback) => {
				callback();
			},
		};

		class UserDropdown extends React.PureComponent {
			methods = {
				//用户dropdown按钮点击触发
				onMenuClick: (item) => {
					// if (item.key === "/logout") {
					// 	window.location.assign(item.key);
					// } else {
					// }
				},
			};
			dropdownMenu = (
				<Menu
					className={cssClass["z-main-user-menu"]}
					selectedKeys={[]}
					onClick={this.methods.onMenuClick && this.methods.onMenuClick}
				>
					<Menu.Item disabled>
						<Icon type="user" />
						个人中心
					</Menu.Item>
					<Menu.Item disabled>
						<Icon type="setting" />
						设置
					</Menu.Item>
					<Menu.Divider />
					<Menu.Item key="/logout">
						<Icon type="logout" />
						退出登录
					</Menu.Item>
				</Menu>
			);
			render() {
				return (
					<Dropdown overlay={this.dropdownMenu} trigger={["click"]} placement="bottomRight">
						<Zlayout.ZheaderBtn className="z-margin-right-15">
							<span className="z-icon-circle z-margin-right-8">
								<Icon type="user" />
							</span>
							登录用户
						</Zlayout.ZheaderBtn>
					</Dropdown>
				);
			}
		}
		const NewMain = ZmainHOC(pageConfig);
		const MyScript = AshowDemoHOC(showMain(NewMain), false);
		return <MyScript />;
	},
});
